@import url(https://fonts.googleapis.com/css?family=ABeeZee|Archivo+Narrow|Cinzel|Nanum+Gothic|Public+Sans&display=swap);

.big-footer {
  height: 280px;
  background-color: rgb(24, 24, 24);
  border-top: 1px solid rgb(68, 68, 68);
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10%;
  text-align: center;
  padding: 2%;
  margin-top: 50px;
}

.contact {
  cursor: pointer;
}

.contact:hover {
  color: rgb(156, 156, 156);
}

@media screen and (max-width: 660px) {
  .big-footer {
    display: grid;
    grid-template-columns: auto;
    grid-gap: 4%;
    height: 540px;
  }
}

.jpg-home{
    max-width:100%;
    max-height:10%;
}

hr {
    background-color: blue;
    height: 2px; 
    width: 200px;
    float: left;
    border: 0;
}

.text-home{
    font-size: 19px;
    margin-left: 4px;
    font-family: 'Cinzel', serif;
    color: rgb(180, 180, 255);
}

.photo-home {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-gap: 10%;
    margin-bottom: 10px;
}

.ins-img {
    height: 150px;
    object-fit: contain;
}

@media screen and (max-width: 1200px) {
    .photo-home {
        display: grid;
        grid-template-columns: auto auto auto;
        grid-gap: 5% 2%;
    }
}

@media screen and (max-width: 992px) {
    .photo-home {
        display: grid;
        grid-template-columns: auto auto;
        grid-gap: 5% 20%;
        justify-content: center;
    }
}

@media screen and (max-width: 770px) {
    .photo-home {
        display: grid;
        grid-template-columns: auto;
        grid-gap: 1% 0%;
    }
}
